<template>
	<div>
		<header>
			{{pageType}}
			<div class="menu-tool" @click="openMenu">
			</div>
			<router-link class="" :to="{name:'edit'}"> 
				<div class="action-add greenWord">
					<i class="iconfont add-icon"></i>
				</div>
			</router-link>
			
		</header>
		<Left_menu :showMenu="showMenu"></Left_menu>
		<div class="page-cover" :class="{'active': showMenu}" @click="showMenu=false"></div>
	</div>
</template>
<script>
	import Left_menu from "../components/Left_menu.vue"
	export default {
		props: {
			pageType: String
		},
		components: {
			Left_menu
		},
		data() {
			return {
				showMenu: false
			}
		},
		methods: {
			openMenu() {
				this.showMenu = true;
			},
			hideMenu() {
				this.showMenu = false;
			}
		}
	}
</script>
<style scoped lang="less">
	.page-cover {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba(0,0,0,.4);
		z-index: 3;
		display: none;
		&.active {
			display: block;
		}
	}
	header {
		font-size: 16px;
		text-align: center;
		line-height: 45px;
		height: 45px;
		border-bottom: 1px solid #eee;
		box-shadow: 0 0 3px #d6d6d6;
		position: fixed;
		width: 100%;
		background: #fff;
		.menu-tool {
			width: 45px;
			height: 45px;
			position: absolute;
			left: 0;
			top: 7px;
			background: url(../assets/images/components/nav_icon.png) no-repeat center ;
			width: 38px;
			height: 32px;
			background-size: 19px 16px;
		}
		.action-add {
			position: absolute;
			right: 10px;
			top: 0;
		}
	}
</style>


