/**
 * 获取标签文字
 * @param  {[String]}  tab  [类型]
 * @param  {[Boolean]} top  [是否置顶]
 * @param  {[Boolean]} good [是否精华]
 * @return {[String]}       [label文字]
 */
export const getLabel = (tab, top, good) => {
	let str = "";

	if (top) {
		str = "置顶";
	} else if (good) {
		str = "精华";
	} else {
		switch (tab) {
			case "ask":
				str = "问答";
				break;
			case "share":
				str = "分享";
				break;
			case "job":
				str = "招聘";
				break;
			case "good":
				str = "精华";
				break;
			default:
				str = "默认";
		}
	}
	return str;
}

/**
 * 根据ISO或者GMT字符串， 获取 是 多久前（对比现在的时间）
 * @param {string} timeString ISO或者GMT字符串
 * @return {string} 年 或 月 或 日
 */
export const getTimeFromNow = (timeString) => {
	// 毫秒数
	const a = (new Date(timeString)).getTime();
	const b = Date.now();
	const ms = b - a;
	// 间隔的秒数
	let second = Math.floor(ms / 1000);
	let year = Math.floor(second / (60 * 60 * 24 * 365)); //年
	let month = Math.floor((second - year * (60 * 60 * 24 * 365)) / (60 * 60 * 24 * 30)); //月
	let day = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30)) / (60 * 60 * 24)); //日
	let hour = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30) - day * (60 * 60 * 24)) / (60 * 60)); //时
	let minute = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30) - day * (60 * 60 * 24) - hour * (60 * 60)) / 60); //分
	let remainSecond = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30) - day * (60 * 60 * 24) - hour * (60 * 60) - minute * 60)); //秒

	if (year > 0) {
		return `${year}年前`;
	} else if (month > 0) {
		return `${month}月前`;
	} else if (day > 0) {
		return `${day}天前`;
	} else if (hour > 0) {
		return `${hour}小时前`;
	} else if (minute > 0) {
		return `${minute}分钟前`;
	}  else if (remainSecond > 0) {
		return `${remainSecond}秒前`;
	} else {
		return `0秒前`;
	}
}


