<template>
	<div>
		<Top_header :pageType="'登录'" ref="head"></Top_header>
		<section class="g-page login-page">
			<input type="text" placeholder="输入您的Access Token" class="login-input" v-model="token">
			<button class="confirm" @click="gotologin">
				确认
			</button>
		</section>
		<div class="" v-if="tips">
			<Tip class="" :msg="tips"></Tip>
		</div>
	</div>
</template>
<script>
	const axios = require("axios");
	// import store from '../../store'
	import Top_header from "../../components/Top_header.vue"
	import Tip from "../../components/Tip.vue"
	import * as util from "../../lib/util.js"

	export default {
		data() {
			return {
				token: '',
				tips: ""
			}
		},
		methods: {
			gotologin() { //8e6503b8-8dae-4b1b-9267-d1246ec11cbf
				let self = this;
				axios.post("https://cnodejs.org/api/v1/accesstoken", {
					accesstoken: this.token
				})
				.then((data)=>{
					this.tips = "登录成功！";
					setTimeout(()=>{self.tips = ""}, 400);
					//多给一个 accessToken
					data.data.accessToken = self.token;
					// 保存userinfo
					this.$store.dispatch("setUserInfo", data.data);
					// 设置到sessionStorage
					window.sessionStorage.setItem("userInfo", JSON.stringify(data.data));
					const returnUrl = self.$route.query.returnUrl ? self.$route.query.returnUrl : "/";
					//重定向
					this.$router.push({
						path: returnUrl
					});
				})
				.catch((err)=>{
					this.tips = "登录失败！";
					setTimeout(()=>{self.tips = ""}, 400);
				})
			}
		},
		components: {
			Top_header,
			Tip
		},
		watch: {
			token: function(val, oldVal){
				this.token = val.trim();
			}
		},
		created() {
		}
	}
</script>
<style lang="less" scoped>
	.login-page {
		font-size: 20px;
	}
	.login-input {
		display: block;
		margin: 190px auto 50px;
		border: 1px solid #42b983;
		width: 80%;
		line-height: 30px;
		padding: 0 10px;
		font-size: 16px;
	}
	.tips {
		line-height: 40px;
		height: 40px;
	}
	.confirm {
		outline: none;
		display: block;
		margin: 0 auto;
		font-size: 17px;
		background: #42b983;
		color: #fff;
		border: none;
		    padding: 11px 150px;
		border-radius: 6px;
	}
</style>


