<template>
	<div>
		<aside class="left-menu" :class="{'showMenu': showMenu}">
			<div class="" v-if="!userInfo">
				<router-link to="/login">
					<div class="user-login" >
						登录
					</div>
				</router-link>
			</div>
			<div class="user-info" v-else>
				<img :src="userInfo.avatar_url" alt="" class="avatar_img">
				<div>
					<span class=" db">{{userInfo.loginname}}</span>
					<span class="logout greenWord db" @click="logout">退出</span>
				</div>

			</div>
			<section class="list-ul">
				<router-link class="icon-quanbu iconfont item" to="/list?tab=all">全部</router-link>
				<router-link class="icon-hao iconfont item" to="/list?tab=good">精华</router-link>
				<router-link class="icon-fenxiang iconfont item" to="/list?tab=share">分享</router-link>
				<router-link class="icon-wenda iconfont item" to="/list?tab=ask">问答</router-link>
				<router-link class="icon-zhaopin iconfont item" to="/list?tab=job">招聘</router-link>
				<router-link class=" item item_dev greenWord" to="/list?tab=dev">测试专用</router-link>
				<router-link class="icon-xiaoxi iconfont item line" to="/list?tab=message">消息</router-link>
				<router-link class="icon-about iconfont item" to="/list?tab=about">关于</router-link>
			</section>
		</aside>
	</div>
</template>
<script>
	import {
		mapGetters
	} from 'vuex';

	export default {
		props: {
			showMenu: Boolean
		},
		data() {
			return {
				userInfo: null
			}
		}, 
		created() {
			this.userInfo = this.$store.getters.getUserInfo;
		},
		methods: {
			logout(){
				this.$store.dispatch("setUserInfo", null);
				window.sessionStorage.removeItem("userInfo");
				this.$router.replace({
					"name": "root"
				});
				this.userInfo = null;
				window.location.href = "/";
			}
		}
	}
</script>
<style scoped lang="less">
	.left-menu {
		position: fixed;
		left: 0;
		top: 0;
		width: 50%;
		z-index: 4;
		height: 100%;
		background: #fff;
		left: -50%;
		transition: all .2s;
		&.showMenu {
			left: 0;
		}
	}
	.user-login {
		padding: 15px;
		font-size: 15px;
		color: #313131;
	}
	/*侧边栏列表*/

	.list-ul {
		margin: 0 15px;
		border-top: 1px solid #d4d4d4;
		overflow: hidden;
		padding-top: 9%;
		.item {
			display: block;
			font-size: 14px;
			font-weight: 200;
			padding: 9% 0;
			text-align: left;
			text-indent: 1px;
			line-height: 15px;
			color: #313131;
			font-weight: 700;
			&:last-child {
				margin-bottom: 50px;
			}
			&:before {
				color: #2c3e50;
			}
		}
		.line {
			border-top: 1px solid #d4d4d4;
		}
	}
	.left-menu {
		.avatar_img {
			width: 60px;
			height: 60px;
			border-radius: 50%;
		}
		.user-info {
			margin: 0 15px;
			padding: 15px 0;
			justify-content: space-around;
			display: flex;
			align-items: center;
			span {
				line-height: 30px;
				font-size: 14px;
			}
		}
	}
	.item.item_dev {
		padding-left: 44px;
		// font-weight: normal;
	}
</style>


